body{
  background-color: #000;
}

.App{
  background-color: #1e1e1e;
  min-height: 600px;
  width: 400px;
  margin: 40px auto;
  border-radius: 10px;
}

#to-do-form input{
  background-color: #5c005c;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 8px;
  color: white;
}

#to-do-form input::placeholder{
  color: #C3AAC8;
}

#to-do-form input:focus, #to-do-form button input:focus{
  outline: none;
}

#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 8px;
  border: 0;
  background-color:#5c005c ;
  cursor: pointer;

  font-size: 16px;
  color: #C3AAC8;
  
}